html {
  box-sizing: border-box;
  border: 0;
  font-weight: 300;
}

.bar {
  background: #fff;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  width: auto;
  height: 70px;
  margin: 20px 0 0;
  padding: 0;
  position: relative;
  box-shadow: 0 1px 8px #3d4e611a;
}

.bar--small {
  height: 42px;
}

.bar:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.button {
  background-color: #0000;
  border: 1px solid #0000;
  width: 19px;
  height: 19px;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 25px;
  left: 26px;
}

.button--small {
  width: 11px;
  height: 11px;
  top: 15px;
  left: 13px;
}

.button--image {
  border: 0;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.button--image_small {
  width: 11px;
  height: 11px;
}

.input {
  background-color: #0000;
  border: 0;
  width: calc(100% - 62px);
  height: 19px;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 22px;
  position: absolute;
  top: 25px;
  left: 62px;
}

.input::placeholder {
  color: #3d4e61;
}

.input--small {
  width: calc(100% - 33px);
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  top: 12px;
  left: 33px;
}

.bar:focus-within {
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
}

.input:focus {
  filter: drop-shadow(0 4px 4px #00000040);
  outline: none;
  font-weight: 800;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-style: normal;
  font-weight: 300;
}
